.footer-nav {
  @extend .flex-wrap;
  margin-bottom: 30px;

  @include m-b-tablet-lg-down {
    max-width: 550px;
    margin: 0 auto;
    margin-bottom: 30px;
  }

  @include m-b-down(634px) {
    flex-direction: column;
    align-items: center;
  }

  &__item {
    flex-grow: 1;

    a {
      @extend .flex-centered-content;
      padding: 10px;
      color: var(--white-color);

      &.active-nav {
        opacity: 0.6;
        color: var(--warning-color);
      }

      transition: color var(--main-t-rules);

      &:hover,
      &:focus {
        color: var(--warning-color);
      }
    }

    @include m-b-tablet-lg-down {
      margin: 0 15px;
      &--offset {
        &-left {
          padding-left: 20px;
        }

        &-right {
          padding-right: 20px;
        }
      }
    }

    @include m-b-down(634px) {
      min-width: 100px;
      margin: 0 15px;
      padding: 0;
    }
  }
}
